// mixin
$light-gray: #b1b1b1;

.main {
  .company {
    border-collapse: collapse;
    tr {
      width: auto;
      height: 5rem;
      font-size: 1.4rem;
      th {
        width: 10rem;
        height: auto;
        border-bottom: 1px solid #fff;
        background: $light-gray;
        color: #fff;
      }
      td {
        width: 56rem;
        height: auto;
        border: 1px solid $light-gray;
        padding-left: 1rem;
      }
      &:last-child {
        th {
          border-bottom: 1px solid $light-gray;
        }
      }
    }
  }
}