.main {
  .form {
    .contact {
      margin-bottom: 2rem;
      font-size: 1.4rem;
      tr {
        width: auto;
        height: 7rem;
        th {
          width: 15rem;
          height: auto;
          font-weight: 600;
        }
        td {
            input[type="text"] ,textarea {
            width: 48rem;
            height: auto;
          }
          .label {
            padding-left: 1rem;
            &:first-of-type {
              margin-right: 1.5rem;
            }
          }
        }
      }
    }
    p {
      text-align: center;
      input[type="submit"] {
        padding: 1rem;
        background: #40a0ae;
        border: none;
        border-radius: 0;
        outline: none;
        appearance: none;
        font-size: 1.6rem;
        color: #fff;
        cursor: pointer;
        &:hover {
          opacity: .6;
        }
      }
    }
  }
}