@charset 'utf-8';
// import
@import '_reset.scss';
@import '_company.scss';
@import '_contact.scss';

// mixin
$normal-pink: #f5b3b4;
$light-pink: #f9e5e4;
$light-gray: #b1b1b1;

// layout
#container {
  width: 980px;
  height: auto;
  min-height: 100%;
  margin: 0 auto;
  position: relative;
}

// header
.header {
  width: auto;
  height: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: $normal-pink;
  h1 {
    span {
      display: none;
    }
    .logo_image {
      width: 110px;
      height: auto;
      vertical-align: middle;
    }
  }
}

// nav
.nav {
  .top_menu {
    display: flex;
    a {
      padding: 1.5rem;
      font-size: 1.4rem;
      font-weight: 600;
      color: #fff;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

// wrapper
.wrapper {
  margin-top: 2rem;
  padding-bottom: 10rem;
  display: flex;
}

// main
.main {
  width: 660px;
  height: auto;
  .content {
    h2 {
      line-height: 2.0;
      font-size: 2rem;
    }
    p {
      line-height: 2.0;
      font-size: 1.6rem;
    }
    .news {
      width: auto;
      height: 20rem;
      padding: 2rem;
      margin-bottom: 1rem;
      line-height: 2.0;
      background: $light-pink;
      font-size: 1.4rem;
      overflow: scroll;
    }
    .banner_content {
      display: table;
      margin-bottom: 2rem;
      p {
        display: table-cell;
      }
    }
  }
}

// aside
.side_menu {
  width: 300px;
  height: auto;
  margin-right: 2rem;
  ul {
    li {
      width: auto;
      height: 6rem;
      margin-bottom: 1rem;
      background: $normal-pink;
      a {
        display: block;
        padding: 2rem;
        font-size: 1.4rem;
        font-weight: 600;
        color: #fff;
      }
      &:last-child {
        margin-bottom: 2rem;
      }
      &:hover {
        opacity: .6;
      }
      &.current {
        background: #fcecec;
        a {
          color: #4d4d4d;
        }
      }
    }
  }
  .banner {
    &:hover {
      opacity: .6;
    }
  }
}

// footer
.footer {
  display: table;
  width: 100%;
  height: 10rem;
  background: #4d4d4d;
  position: absolute;
  bottom: 0;
  p {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
    font-size: 1rem;
    color: #fff;
  }
}